import React, { useState } from "react";
import { useMutation } from "@tanstack/react-query"
import {
  WrapperContainer,
  WrapperContainerLeft,
  WrapperContainerRight,
  WrapperTextLight,
} from "./style";
import { EyeTwoTone, EyeInvisibleOutlined } from "@ant-design/icons";
import InputForm from "../../../components/InputForm/InputForm";
import ButtonComponent from "../../../components/ButtonComponent/ButtonComponent";
import { Image } from "antd";
import imgLogin from "./../../../assets/common/images/logo-login.png";
import styled from "styled-components";
import Link from "antd/es/typography/Link";
import * as UserService from "../../../services/UserService"

const TextContent = styled.p`
  color: var(--cparagraph);
`;

const TitlePage = styled.h2`
  color: var(--cheadline);
`;

const SignInPage = () => {
  const [isShowPassword, setIsShowPassword] = useState(false);
  const [email,setEmail] = useState("");
  const [password,setPassword] = useState("");

  const mutation = useMutation({
    mutationFn: data => UserService.loginUser(data)
  })

  console.log('mutation ',mutation);
  

  const handleLogin = () =>{
    mutation.mutate({
      email,
      password
    })
    console.log("login");
  }

  return (
    <WrapperContainer>
      <div
        style={{
          width: "800px",
          height: "400px",
          display: "flex",
          borderRadius: "10px",
          backgroundColor: "#fff",
        }}
      >
        <WrapperContainerLeft>
          <TitlePage>Xin cho</TitlePage>
          <TextContent>Mi bn ng nhp ti khon</TextContent>
          <InputForm placeholder={"Email hoc ti khon"} value={email} type="text" onChange={(e) => setEmail(e.target.value)}/>
          <div style={{ position: 'relative' }}>
            <span
              onClick={() => setIsShowPassword(!isShowPassword)}
              style={{
                zIndex: 10,
                position: 'absolute',
                top: '4px',
                right: '8px',
              }}
            >
              {isShowPassword ? (<EyeTwoTone/>) : (<EyeInvisibleOutlined />)}
            </span>
            <InputForm
              placeholder={"Mt khu"}
              value={password}
              type={isShowPassword ? "text" : "password"}
              onChange={(e)=>setPassword(e.target.value)}
            />
          </div>

          <ButtonComponent
            disabled={ !email.length || !password.length}
            onClick={handleLogin}
            size={40}
            styleButton={{
              backgroundColor: 'var(--cbutton)',
              height: "48px",
              width: "100%",
              border: "none",
              borderRadius: "4px",
            }}
            styleTextButton={{
              color: 'var(--cbuttontext)',
              fontSize: "20px",
              fontWeight: "600",
            }}
            textButton={"ng nhp"}
          />
          <Link>
            <WrapperTextLight>Qun mt khu</WrapperTextLight>
          </Link>
        </WrapperContainerLeft>
        <WrapperContainerRight>
          <Image src={imgLogin} width="250px" height="250px" preview={false} />
        </WrapperContainerRight>
      </div>
    </WrapperContainer>
  );
};

export default SignInPage;
